	module.exports = {"data":{"markdownRemark":{"html":"<p>Renovate two rooms, one is the conference room, and the other is the administrative process room. There are several graduate students who volunteered. Almost a day. Painted the walls in meeting room 613, then painted the floor. I am going to brush something called \"crystal paint\" tomorrow. Nothing in the past, it seems to be the latest development. This kind of thing is commonly used in many places abroad. Try it first, if it's good, consider using it elsewhere.</p>\n<p>A department must arm itself to a first-class level, including hardware. In this regard, it is necessary for the whole school to consider the reality. There are two main aspects to running a school. One is software, the creation of knowledge and ideas, and the other is hardware, which is the modernization of equipment and facilities. We are now facing challenges in both areas. How to manage the international politics department? The modernization of the country is composed of the modernization of one unit.</p>\n<p>Some people have such problems, and sometimes everything has to wait for the government to do it, and the initiative to do it is not high. Such a situation is the cause of personal culture, or the inertia caused by social regulation.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0050.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0050.md","frontmatter":{"title":"March 5th Saturday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0050.md absPath of file >>> MarkdownRemark"}}

